<template>
    <div class="findpassword">
        <BgBox>
            <van-cell-group>
            <van-field v-model="mailValue" label="邮箱" placeholder="请输入邮箱" @blur="verifyMail"/>
            <van-field v-model="verifyMailValue" label="邮箱验证码" placeholder="请输入邮箱验证码">
                <template #button>
                    <van-button size="small" type="primary" @click="sendMailCode" color="linear-gradient(to right, #ff6034, #ee0a24)">发送验证码</van-button>
                </template>
            </van-field>
            </van-cell-group>
            <van-button type="primary" size="large" round color="linear-gradient(to right, #ff6034, #ee0a24)" @click="commit">下一步</van-button>
        </BgBox>   
    </div>
</template>

<script>
    import {verifyForm} from '../assets/js/verifyForm.js'
    import BgBox from "../components/BgBox.vue"
    export default {
        name: 'FindPassword',
        components: {
            BgBox
        },
        data() {
            return {
                mailValue: '',
                verifyMailValue: ''
            }
        },
        methods: {
            verifyMail(){
                
                let formData = {
                    mail: {
                        value: this.mailValue,
                        errorMsg: '请输入有效邮箱',
                        reg: /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
                    },
                }
                let isPass = verifyForm.verify(formData);
                if(isPass){
                    
                }
            },
            sendMailCode(){
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                //获取邮箱验证码
                this.axios({
                    method: 'POST',
                    url: '/emailValidCode',
                    data: {
                        appkey: this.appkey,
                        email: this.mailValue
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == "J001"){
                        
                    }
                    this.$toast(res.data.msg);
                }).catch(err => {
                    this.$toast.clear();
                })
            },
            commit(){
                 this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })

                //验证邮箱验证码
                this.axios({
                    method: 'POST',
                    url: '/checkValidCode',
                    data: {
                        appkey: this.appkey,
                        validCode: this.verifyMailValue
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == "K001"){
                        this.$router.push({name: 'ModifyPassword'})
                    }
                    this.$toast(res.data.msg);
                }).catch(err => {
                    this.$toast.clear();
                })
            }
        },
    }
</script>

<style lang="less" scoped>
     /deep/.van-button--danger {
        background: linear-gradient(to right, #ff6034, #ee0a24);
    }
</style>